.PanelHeaderMyRecordMenu{
	color: #00A897;
	font-family: "Verdana";
}
.MainMyRecordMenu{
	background: #FFF; 
  	box-sizing: border-box;
  	position: absolute;
  	width: 100%;
  	height: 90vh;
}
.ButtonBlocksMyRecordMenuDataTime{
	margin-right: 10vw;
	margin-left: 10vw;
}
.ButtonMyRecordMenuDataTime{
	margin-top: 5%;
	margin-bottom: 5%;
	background-color: #E1DFE0;
	border-color: #E1DFE0;
	border-radius: 0px;
}
.TextButtonMyRecordMenuDataTime{
	color: #000;
	font-family: "Georgia";
	font-size: 18pt;
	padding: 0px 0px 0px 0px;
	margin: 0px 0px 0px 0px;
}
.HiddenBlock{
	display: none;
	height: 100px;
	width: 100%;
	padding: 0px 0px 0px 0px;
	margin: 0px 0px 0px 0px;
	text-align: center;
}
.ButtonMyRecordMenuRecord{
	margin-top: 5%;
	margin-bottom: 5%;
	margin-left: 1%;
	margin-right: 1%;
	background-color: #E1DFE0;
	border-color: #E1DFE0;
	border-radius: 70px;
}
.DeleteButton{
	width: 70px;
	margin-left: 2%;
	margin-right: 2%;
	background-color: #00A897;
	font-family: "Georgia";
	font-size: 16pt;
	color: white;
}
