.PanelHeaderScheduleMenu{
	color: #00A897;
	font-family: "Verdana";
}
.MainScheduleMenu{
	background: #FFF; 
  	box-sizing: border-box;
  	position: absolute;
  	width: 100%;
  	height: 90vh;
}
.DataRecord{
	font-family: "Georgia";
	font-size: 20pt;
	text-align: center;
	border-color: black;
	border-width: 2px;
	border: darkblue;
	margin-top: 10%;
}
.TextDataRecord{
	margin-bottom: 3%;
}
.ButtonBlocksScheduleMenuDay{
	display: block;
	text-align: center;
}
.ButtonScheduleMenuDay{
	margin-left: 1%;
	margin-right: 1%;
	width: 70px;
	background-color: #E1DFE0;
	border-color: #E1DFE0;
	border-radius: 25px;
}
.TextButtonScheduleMenuDay{
	color: #00A897; 
	font-family: "Georgia";
	padding: 0px 0px 0px 0px;
	margin: 0px 0px 0px 0px;
}
.TextScheduleMenuFreeTime{
	text-align: center;
	margin-top: 10%;
	font-family: "Georgia";
	font-size: 20pt;
}
.ButtonBlocksScheduleMenuTime{
	display: block;
	text-align: center;
}
.ButtonScheduleMenuTime{
	margin-left: 3%;
	margin-right: 3%;
	background-color: #00A897;
	border-color: #00A897;
	border-radius: 0px;
	height: 40px;
}
.TextButtonScheduleMenuTime{
	color: #FFF; 
	font-family: "Georgia";
	font-size: 15pt;
	padding: 0px 0px 0px 0px;
	margin: 0px 0px 0px 0px;
}
.ButtonСonfirmSheduleMenu{
	text-align: center;
	margin-top: 5%;
	margin-bottom: 5%;
	border-color: #00A897; 
	color: #FFF;
	background-color: #00A897;
}
.TextButtonСonfirmSheduleMenu{
	font-family: "Georgia";
	font-size: 14pt;
	padding: 0px 0px 0px 0px;
	margin: 0px 0px 0px 0px;
}
.TextAlertConfirm{
	font-family: "Georgia";
	font-size: 14pt;
	text-align: center;
}
